.nounderline {
  text-decoration: none !important
}
.jumbotron {
	/*  Background fallback in case of IE8 & down, or in case the image doesn't load, such as with slower connections  */
	background: url(/clicky-game/static/media/banner-background.0c919e89.jpg) center center no-repeat fixed #464646;

	font-family: Serif, Monospace;
	height: 400px;
	width: auto;
	opacity: 0.5;
}

h1, h4 {
	color: #ffffff;
	font-weight: bold;
}

.imageBlock {
	cursor: pointer;
	border: solid 1px black;
	background: #AEAEAE;
	-webkit-transition: all .3s ease-in-out;
	transition: all .3s ease-in-out;
}

.imageBlock:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1); /* (110% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
	background: #FFFFFF;
}
.footer {
  min-height: 100px;
  background: #E5E5E5;
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #B8B8B8;
}

